.message {
  display: flex;
  flex-direction: column;
  justify-content: end;
}

.imageAndText {
  color: #FFF;
  font-family: Montserrat;
  display: flex;
  flex-direction: row-reverse;
  gap: 10px;
}

.imageAndText img {
  width: 48px;
  height: 48px;
}

.text {
  background-color: #06C;
  border-radius: 5px;
  position: relative;
}

.text:after {
  content: "";
  position: absolute;
  border: 10px solid transparent; 
  border-bottom: 10px solid #06C;
  border-left: 4px solid #06C;
  bottom: 0px;
  right: -8px;
}

.name {
  padding: 2px 10px;
  text-align: end;
  font-family: Montserrat;
  font-size: 18px;
  font-style: normal;
  font-weight: 600;
  line-height: normal;
}

.messageText {
  padding: 2px 10px;
  font-family: Montserrat;
  font-size: 16px;
  font-style: normal;
  font-weight: 400;
  line-height: normal;
}

.time {
  display: flex;
  flex-direction: row-reverse;
  padding: 6px 10px;
  color: #000;
  font-family: Montserrat;
  font-size: 10px;
  font-style: normal;
  font-weight: 600;
  line-height: normal;
}